<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <el-form :inline="true" :model="searchValidateForm" ref="searchValidateForm" label-width="100px" class="search-validate-form" :rules="searchValidateFormRules">
          <el-form-item label="" prop="companyName">
            <!-- 输入公司名称 start -->
            <el-input class="input-name" v-model="searchValidateForm.companyName" placeholder="公司名称" @change="onChange"></el-input>
            <!-- 输入公司名称 end -->
          </el-form-item>
        </el-form>

        <!-- 搜索 start -->
        <el-button type="primary" icon="el-icon-search" @click="onSearch('searchValidateForm')">搜索</el-button>
        <!-- 搜索 end -->

        <!-- 新增 start -->
        <el-button type="primary" icon="el-icon-circle-plus-outline" @click="onAdd">新增</el-button>
        <!-- 新增 end -->

        <!-- 导出 Excel start -->

        <!-- <download-excel class="export-btn" :data="tableData" :fields="tableFields" type="xls" header="合作公司" name="合作公司.xls">
          <el-button type="primary" icon="el-icon-upload">
            导出Excel
          </el-button>
        </download-excel> -->
        <el-button type="primary" :loading="downloadLoading" icon="el-icon-top-right" @click="exportExcel">导出Excel</el-button>

        <!-- 导出 Excel end -->

        <!-- 导入 Excel start -->
        <el-upload class="upload-demo" action="" :on-change="handleChange" :on-exceed="handleExceed" :on-remove="handleRemove" :file-list="fileListUpload" :show-file-list="false" :limit="limitUpload" accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel" :auto-upload="false" v-if="updateUpload">
          <el-button type="primary" icon="el-icon-upload">导入Excel</el-button>
          <!-- <div slot="tip" class="el-upload__tip">只 能 上 传 xlsx / xls 文 件</div> -->
        </el-upload>
        <!-- 导入 Excel end -->

      </el-header>
      <el-main>
        <!-- 表格 start -->
        <el-table class="table" :data="tableData" style="width: 100%" border v-loading="loadingTable" :header-cell-style="{ background: '#f2f2f2',color: '#606266', height: '38px', padding: '0px' }" :cell-style="{height: '38px',padding: '0'}">
          <el-table-column v-for="(value, key) in tableFields" :key="key" :prop="value" :label="key" min-width="130" align="center">
            <template slot-scope="scope">
              <div>
                {{ tableContentValue(scope.row, value) }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="操作" min-width="130">
            <template slot-scope="scope">
              <el-button @click.native.prevent="editRow(scope.$index, scope.row)" type="text" size="small">
                编辑
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 表格 end -->

        <!-- 导出的表格 start -->
        <!-- 此表格始终保持隐藏 -->
        <el-table id="outTable" class="table" :data="exportTableData" style="width: 100%; display: none" border>
          <el-table-column v-for="(value, key) in tableFields" :key="key" :prop="value" :label="key" min-width="130" align="center">
          </el-table-column>
        </el-table>
        <!-- 导出的表格 end -->
      </el-main>
      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination background layout="prev, pager, next, total, sizes, jumper" :total="total" :current-page="currentPage" :page-sizes="[10, 20, 30, 40, 50, 60, 70, 80, 90]" :page-size="pageSize" @current-change="onCurrentChange" @size-change="onSizeChange">
          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>

      <!-- dialog对话框 start -->
      <el-dialog class="dialog" :title="dialogTitle" :visible.sync="dialogVisible" :before-close="handleClose" :close-on-click-modal="false">
        <!-- form表单 -->
        <el-form :model="validateForm" ref="validateForm" label-width="100px" class="demo-ruleForm" :rules="validateFormRules">
          <!-- 合作公司名称 start -->
          <el-form-item label="合作公司名称" prop="companyName">
            <el-input type="text" v-model="validateForm.companyName" autocomplete="off" placeholder="请输入合作公司名称"></el-input>
          </el-form-item>
          <!-- 合作公司名称 end -->

          <!-- 收款账户 start -->
          <el-form-item label="收款账户" prop="collectionAccount">
            <el-input type="text" v-model="validateForm.collectionAccount" autocomplete="off" placeholder="请输入收款账户"></el-input>
          </el-form-item>
          <!-- 收款账户 end -->

          <!-- 收款账号 start -->
          <el-form-item label="收款账号" prop="collectionAccountNumber">
            <el-input type="text" 
            :value="validateForm.collectionAccountNumber" @input="onInputCollectionAccountNumber" autocomplete="off" placeholder="请输入收款账号"></el-input>
          </el-form-item>
          <!-- 收款账号 end -->

          <!-- 开户行 start -->
          <el-form-item label="开户行" prop="bank">
            <el-input type="text" v-model="validateForm.bank" autocomplete="off" placeholder="请输入开户行"></el-input>
          </el-form-item>
          <!-- 开户行 end -->

          <!-- 备注 start -->
          <el-form-item label="备注" prop="notes">
            <el-input type="text" v-model="validateForm.notes" autocomplete="off" placeholder="请输入备注"></el-input>
          </el-form-item>
          <!-- 备注 end -->
        </el-form>
        <!-- form表单 -->
        <!-- 提交 start -->
        <div class="submit">
          <el-button type="primary" @click="submitForm('validateForm')">提交</el-button>
          <el-button @click="dialogClose">取消</el-button>
        </div>
        <!-- 提交 end -->
      </el-dialog>
      <!-- dialog对话框 end -->
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'
export default {
  name: 'PartnerCompany',
  data() {
    /**
    * 公共验证函数(验证数字值)
    * @params message - 提示信息
    */
    function checkNumberMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback();
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'));
        } else {
          callback();
        }
      }
    }

    /**
    * 公共验证函数(验证必填数字值)
    * @params message - 提示信息
    */
    function checkRequireNumberMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'));
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'));
        } else {
          callback();
        }
      }
    }

    /**
    * 公共验证函数(验证必填收款账号)
    * @params message - 提示信息
    */
    function checkRequireCANumberMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'));
        }

        if (isNaN(Number(value.replace(/ /g, "")))) {
          callback(new Error(name + '必须为数字值'));
        } else {
          callback();
        }
      }
    }

    /**
    * 公共验证函数(验证必填值)
    * @params message - 提示信息
    */
    function checkRequireMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'));
        } else {
          callback();
        }
      }
    }

    return {
      // input: '', // 请输入公司名称
      searchCompanyName: "", // 搜索的公司名称
      tableFields: { //导出Excel表格的表头设置
        "合作公司名称": "companyName",
        "收款账户": "collectionAccount",
        "收款账号": "collectionAccountNumber",
        "开户行": "bank",
        "创建时间": "createTime",
        "备注": "notes"
      },
      tableData: [], // 表格数据
      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画

      loadingTable: false,

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      dialogVisible: false, // 是否显示dialog对话框
      dialogState: "", // dialog是新增还是编辑表单，新增为'add'，编辑为'edit'

      searchValidateForm: {
        companyName: ""
      }, // 搜索表单验证对象
      searchVF: {
        companyName: ""
      }, // 搜索需要提交的表单
      validateForm: {},
      total: 0,

      dialogTitle: "",

      limitUpload: 3,
      file: null, // 上传的Excel文件
      fileTemp: null, // 上传的临时Excel文件
      fileListUpload: [], // 上传Excel文件列表

      updateUpload: true, // 是否显示upload组件

      // 提交表单验证规则
      validateFormRules: {
        // 合作公司名称
        companyName: [
          { validator: checkRequireMethod('合作公司名称'), required: true, trigger: ['blur', 'change'] }
        ],

        // 收款账户
        collectionAccount: [
          { validator: checkRequireMethod('收款账户'), required: true, trigger: ['blur', 'change'] }
        ],

        // 收款账号
        collectionAccountNumber: [
          { validator: checkRequireCANumberMethod('收款账号'), required: true, trigger: ['blur', 'change'] }
        ],

        // 开户行
        bank: [
          { validator: checkRequireMethod('开户行'), required: true, trigger: ['blur', 'change'] }
        ],


      },

      // 搜索表单验证规则
      searchValidateFormRules: {

      }
    }
  },
  created() {
    // 如果当前环境是开发环境才调用登录方法
    if (process.env.NODE_ENV == 'development') {
      this.login()
    }

    this.initTableData()
    this.getValidateForm()
  },
  methods: {
    // 获取表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ""
      }
      this.validateForm = validateForm
      // console.log(this.validateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post("/loginSystem?username=lijiazhao&password=lijiazhao")
    },

    /**
     * 初始化表格数据
     */
    async initTableData() {
      var companyName = this.searchVF.companyName
      if (companyName == "" || !companyName) {
        companyName = null
      }

      this.loadingTable = true
      const { data: res } = await this.$http.get("/system/cooperative_company/list", {
        params: {
          name: companyName, // 公司名称(模糊查询)
          // this.currentPage
          pageNum: (this.currentPage - 1) * this.pageSize, // 页码
          pageSize: this.pageSize // 每页条数
        }
      })
      // console.log(res);
      if (res.state == "200") {
        this.tableData = res.data
        this.total = res.total
        // this.$message({
        //   message: res.msg,
        //   type: 'success',
        //   offset: 100,
        //   center: true,
        //   duration: 1000,
        // })
        this.loadingTable = false
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000,
        })
        return
      }
    },

    /**
     * 新增表格数据
     */
    async addTableData() {
      const { data: res } = await this.$http.post("/system/cooperative_company/add", this.validateForm)
      if (res.state == "200") {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000,
        });
        this.initTableData()
        this.$refs.validateForm.resetFields() // 重置表单
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000,
        });
      }
    },

    /**
     * 修改表格数据
     */
    async editTableData() {
      const { data: res } = await this.$http.post("/system/cooperative_company/edit", this.validateForm)
      if (res.state == "200") {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000,
        });
        this.initTableData()
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000,
        });
      }
    },

    /**
     * 获取导出表格的全部数据
     */
    async getExportTableData() {
      var companyName = this.searchVF.companyName
      if (companyName == "" || !companyName) {
        companyName = null
      }

      const { data: res } = await this.$http.get("/system/cooperative_company/list", {
        params: {
          name: companyName, // 公司名称(模糊查询)
        }
      })
      // console.log(res);
      if (res.state == "200") {
        this.exportTableData = res.data
      } else {
        return
      }
    },

    /**
     * 查询公司名称获取列表数据
     */
    getTableData() {
      // 上次查询内容是否等于当前查询内容
      var isMeta = () => {
        for (var i in this.searchValidateForm) {
          if (this.searchValidateForm[i] != this.searchVF[i]) return false
        }
        return true
      }

      // 如果输入框中的内容等于上次搜索的内容，则直接返回
      if (isMeta()) return

      // 保存当前搜索的内容
      this.searchVF = {...this.searchValidateForm}

      // 否则调用接口，请求数据，为tableData重新赋值
      console.log("调用接口");
      this.initTableData()
    },
    // 仅在输入框失去焦点或用户按下回车时触发
    onChange() {
      // this.getTableData()
    },
    // 点击搜索按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.getTableData()
        } else {
          console.log('error submit!!');
          return false;
        }
      })
    },

    // 导出Excel表格
    exportExcel() {
      this.$confirm('确认导出Excel表格?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        this.downloadLoading = true

        // 请求全部数据
        await this.getExportTableData() // 这里使用await异步执行
        // 使用nextTick当table表格渲染完成后执行导出表格操作
        this.$nextTick(() => {
          var xlsxParam = { raw: true } //转换成excel时，使用原始的格式
          /* 从表生成工作簿对象 */
          var wb = XLSX.utils.table_to_book(
            document.querySelector('#outTable'),
            xlsxParam
          )
          /* 获取二进制字符串作为输出 */
          var wbout = XLSX.write(wb, {
            bookType: 'xlsx',
            bookSST: true,
            type: 'array'
          })
          try {
            FileSaver.saveAs(
              //Blob 对象表示一个不可变、原始数据的类文件对象。
              //Blob 表示的不一定是JavaScript原生格式的数据。
              //File 接口基于Blob，继承了 blob 的功能并将其扩展使其支持用户系统上的文件。
              //返回一个新创建的 Blob 对象，其内容由参数中给定的数组串联组成。
              new Blob([wbout], {
                type: 'application/octet-stream'
              }),
              //设置导出文件名称
              "合作公司" + '.xlsx'
            )
          } catch (e) {
            if (typeof console !== 'undefined') console.log(e, wbout)
          }
          this.downloadLoading = false
          return wbout
        })

        this.$message({
          type: 'success',
          message: '已成功导出Excel表格！',
          offset: 100,
          center: true,
          duration: 1000,
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消导出Excel表格',
          offset: 100,
          center: true,
          duration: 1000,
        });
      })

    },

    // 新增
    onAdd() {
      this.dialogVisible = true
      this.dialogState = "add"
      this.dialogTitle = "新增合作公司信息"
    },

    // 点击编辑时触发
    editRow(index, row) {
      // console.log(index, row);
      this.dialogVisible = true // 是否显示编辑dialog对话框
      var rowObj = Object.assign({}, row) // 深拷贝row对象
      // rowObj.collectionAccountNumber = parseFloat(rowObj.collectionAccountNumber)
      this.validateForm = rowObj
      this.dialogState = "edit"
      this.dialogTitle = "编辑合作公司信息"
    },

    // 编辑dialog取消
    dialogClose() {
      this.dialogVisible = false
      this.$refs.validateForm.resetFields(); // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 编辑dialog 右上角关闭按钮
    handleClose(done) {
      done() // 关闭dialog对话框
      this.$refs.validateForm.resetFields(); // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // dialog提交
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          console.log(this.validateForm);
          if (this.dialogState == "add") {
            // 新增公司信息
            console.log("新增公司信息");
            this.addTableData()
          } else if (this.dialogState == "edit") {
            // 编辑公司信息
            console.log("编辑公司信息");
            this.editTableData()
          }
          this.dialogVisible = false
          this.$refs.validateForm.resetFields(); // 重置表单
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData()
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData()
    },

    /**
     * 更新upload组件的方法
     */
    reloadUpload() {
      // this.$forceUpdate()
      // 移除组件
      this.updateUpload = false
      // 在组件移除后，重新渲染组件
      // this.$nextTick可实现在DOM 状态更新后，执行传入的方法。
      this.$nextTick(() => {
        this.updateUpload = true
      })
    },

    // 文件状态改变时的钩子，添加文件、上传成功和上传失败时都会被调用
    handleChange(file, fileList) {
      // console.log(file);
      this.fileListUpload[0] = file
      this.fileTemp = file.raw

      this.reloadUpload()

      // 导入判断
      if (this.fileTemp) {
        if ((this.fileTemp.type == 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet') || (this.fileTemp.type == 'application/vnd.ms-excel')) {
          this.importfxx(this.fileTemp)
        } else {
          this.$message({
            type: 'warning',
            message: '附件格式错误，请删除后重新上传！'
          })
        }
      } else {
        this.$message({
          type: 'warning',
          message: '请上传附件！'
        })
      }
    },

    // 文件超出个数限制时的钩子
    handleExceed(files, fileList) {
      this.$message.warning(`当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
    },

    // 文件列表移除文件时的钩子	
    handleRemove(file, fileList) {
      this.fileTemp = null
      this.fileListUpload = []
    },

    // 导入格式
    importfxx(obj) {
      let _this = this;
      // 通过DOM取文件数据
      this.file = obj
      var rABS = false; //是否将文件读取为二进制字符串
      var f = this.file;
      var reader = new FileReader();
      //if (!FileReader.prototype.readAsBinaryString) {
      FileReader.prototype.readAsBinaryString = function (f) {
        var binary = "";
        var rABS = false; //是否将文件读取为二进制字符串
        var pt = this;
        var wb; //读取完成的数据
        var outdata;
        var reader = new FileReader();
        reader.onload = function (e) {
          var bytes = new Uint8Array(reader.result);
          var length = bytes.byteLength;
          for (var i = 0; i < length; i++) {
            binary += String.fromCharCode(bytes[i]);
          }
          var XLSX = require('xlsx');
          if (rABS) {
            wb = XLSX.read(btoa(fixdata(binary)), { //手动转化
              type: 'base64'
            });
          } else {
            wb = XLSX.read(binary, {
              type: 'binary'
            });
          }
          outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]);//outdata就是你想要的东西
          console.log(outdata);
          this.da = [...outdata]
          let arr = []
          this.da.map(v => {
            let obj = {
              robotCount: v["机器人数量"],
              type: v["发货进度"],
              applicantName: v["申请人"],
              factoryName: v["发货工厂"],
              consignorName: v["发货人"],
              receivingName: v["收货景区"],
              consigneeName: v["收货人"],
              consigneePhone: v["收货人电话"],
              receiningAddress: v["收货地址"],
              consignmentDate: v["发货时间"]
            }
            arr.push(obj)
          })
          // // 将 arr 赋值给 tableData，来更新表格数据
          // this.tableData = arr
          return arr
        }
        reader.readAsArrayBuffer(f);
      }

      if (rABS) {
        reader.readAsArrayBuffer(f);
      } else {
        reader.readAsBinaryString(f);
      }
    },

    onInputCollectionAccountNumber(value) {
      var result = ""
      // 四个数字空一格
      var str = String(value.replace(/ /g, ""))
      var len = 4
      var arr = []
      for(var i = 0; i < str.length; i += len) {
        arr.push(str.slice(i, i + len))
      }
      // console.log(arr);
      result = arr.join(" ")
      // console.log(result);

      this.validateForm.collectionAccountNumber = result
    }
  },
  computed: {
    tableContentValue() {
      return (row, value) => {
        var result = ""
        if (value == "collectionAccountNumber") {
          // 四个数字空一格
          var str = String(row[value])
          var len = 4
          var arr = []
          for(var i = 0; i < str.length; i += len) {
            arr.push(str.slice(i, i + len))
          }
          // console.log(arr);
          result = arr.join(" ")
        } else {
          result = row[value]
        }
        return result
      }
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
  }
}
</script>

